import React, { useEffect, useState } from "react";

const Updateform = (props) => {

  const userProfile = JSON.parse(localStorage.getItem("profile"));

  const [data, setData] = useState({
    name: "userProfile.name",
    firstName: "userProfile.given_name",
    lastName: "props.pro.lastName",
    email: "props.pro.email",
    Mobile: "props.pro.mobile",
    DateofBirth: "props.pro.DateofBirth",
    Gender: "props.pro.Gender",
    image : "props.pro.image",
  });


  const getData = async (email) => {
    
    const response = await fetch(
      `${process.env.REACT_APP_API_URL}/data/fetchdata/${email}`,
      {
        method: "GET",
        headers: {
          "Content-Type": "apppropslication/json",
        },
      }
    );

    let temp = await response.json();
    setData(temp);
  };

  const onChange = (e) => {
    setData({ 
      ...data, 
      [e.target.name]: e.target.value
    });
  };
  const onChangeImage = (e) => {
    setData({ 
      ...data, 
      image:"https://lh3.googleusercontent.com/a/AEdFTp7nefjuVi7lNYzDwETIRydYgnLG6V-KXkaDAlnKZw=s96-c"
    });
  };

  const submitData = async (e) => {
    e.preventDefault();

    let newData = { ...data, email: props.pro.email };

    const response = await fetch(
      `${process.env.REACT_APP_API_URL}/data/updatedata`,
      {
        method: "PATCH",
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
        },
        body: JSON.stringify(newData),
      }
    );
    return response.json();
  };

  useEffect(() => {
    getData(userProfile.email);
  }, []);

  return (
    <div>
      <div style={{ marginLeft: "230px", marginBottom: "30px" }}>
        <h1>Details</h1>
      </div>
      <form onSubmit={submitData}>
        <div className="row my-2">
          <div className="col">
            <input
              type="text"
              name="firstName"
              className="form-control"
              placeholder="First name"
              value={data.firstName}
              onChange={onChange}
            />
          </div>
          <div className="col">
            <input
              type="text"
              name="lastName"
              className="form-control"
              placeholder="Last name"
              value={data.lastName}
              onChange={onChange}
            />
          </div>
        </div>

        <div className="my-2">
          <input
            type="email"
            name="email"
            className="form-control"
            placeholder="Email"
            value={data.email}
            onChange={onChange}
            disabled="true"
          />
        </div>

        <div className="row my-2">
          <div className="col">
            <input
              type="text"
              name="Gender"
              className="form-control"
              placeholder="Gender"
              onChange={onChange}
              value={data.Gender}
            />
          </div>
          <div className="col">
            <input
              type="date"
              name="DateofBirth"
              className="form-control"
              placeholder="Date of Birth"
              onChange={onChange}
              value={data.DateofBirth}
            />
          </div>
          <div className="col">
            <input
              type="integer"
              name="Mobile"
              className="form-control"
              placeholder="Mobile No."
              onChange={onChange}
              value={data.Mobile}
            />
          </div>
        </div>

        <div className="row my-2">
          <div className="col">
            <input
              style={{ height: "100px" }}
              type="text"
              className="form-control"
              placeholder="About me"
              onChange={onChange}
            />
          </div>
          <div className="col">
            <label htmlFor="exampleFormControlFile1">Profile Picture</label>
            <input
              type="file"
              name="image"
              className="form-control-file"
              onChange={onChangeImage}
    
            />
          </div>
        </div>
        <button type="submit">Submit</button>
      </form>
    </div>
  );
};

export default Updateform;
