import { googleLogout, useGoogleLogin } from '@react-oauth/google';
import React, { useState, useEffect } from 'react'
import { useNavigate } from 'react-router';
import axios from 'axios';
import moment from 'moment';

const Googleoauth = (props) => {

    const [user, setUser] = useState([]);
     const [profile, setProfile] = useState([]);
    const [mobile, setMobile] = useState('');
    const [gender, setGender] = useState('');
    const [dob, setDOB] = useState('');
    const navigate = useNavigate();
    const [show,setShow] = useState(false);

    const login = useGoogleLogin({
        onSuccess : (codeResponse) => setUser(codeResponse),
        onError : (error) => console.log("Login Failed", error)
    })

    const authenticate = () => {
        
         axios.post(`${process.env.REACT_APP_API_URL}/data/checkuser`,{email:profile.email})
       .then((res)=>{ 
        if(res.data.status){
            setShow(true)
        }
        else{
            setShow(false)
        }
        
        })

    }
    const handleSubmit = (e) => {
        e.preventDefault();

        // Submit details to backend
        const userDetails ={
            mobile,
            dob,
            gender,
            loginTime:moment().format('YYYY-MM-DD HH:mm:ss a'),
            ...profile 
        };

        axios.post("http://localhost:7000/data/userdetails",userDetails);
        localStorage.setItem('profile',JSON.stringify(userDetails));
        navigate('/home');
    };

    if(profile.email)  {
        authenticate()    // once we have the user details, we need to authenticate the user to displaye and route to the dashboard page
        console.log(profile, "PROFILE");
        localStorage.setItem('profile',JSON.stringify(profile));
    }

    useEffect(
        () =>{
            if(user.access_token){
                axios
                .get(`https://www.googleapis.com/oauth2/v2/userinfo?access_token=${user.access_token}`,{
                    headers : {
                        Authorization : `Bearer ${user.access_token}`,
                        Accept : 'application/json'
                    }
                })
                .then((res) =>{
                    setProfile(res.data);
                    props.setPro(res.data);
                    console.log(res.data,"res.data");
                    
                })
                .catch((error) => { console.log(error); })
            }
        },
        [user]
    );

       
    


    // Logout function to logged out user
    const logOut = () => {
        googleLogout();
        localStorage.clear();
        setProfile(null);
        navigate("/")
    };


  return (
    <div className ="container d-flex align-items-center justify-content-center" style ={{marginTop : "250px",marginLeft: "625px" ,backgroundColor: "lightgrey",width : "600px", height:"500px"}}>
      
      <br />
      <br />

      {profile.email? (
        <>
        {
            show?
            (navigate('/home')):
            
        (<div>
            <img src={profile.picture} alt ="Profile Picture" />
            <h3>{profile.name} LoggedIn</h3>
            <p>Name : {profile.name}</p>
            <p>Email : {profile.email}</p>
            <button onClick={logOut}>logout</button>
            <br />
            <br />
        </div>)
        }
        </>
        ):(
            <>
            <h2>Login with Google</h2>
            <br/>
            <br/>
            <button onClick={login}>
                <img src = "../../../image/Google.png" alt = "Google Logo" />
                SignIn with Google
            </button>
            </>
        )
      }
      {
        user.access_token && (
            <form onSubmit = {handleSubmit}>
                <h3>Enter Required Details</h3>
                <label htmlFor ="mobile">Mobile No.:</label>
                <input type ="tel" name ="mobile" value ={mobile} onChange ={(e) => setMobile(e.target.value)} required />

                <label htmlFor ="dob">Date of Birth.:</label>
                <input type ="date" name ="dob" value ={dob} onChange ={(e) => setDOB(e.target.value)} required />

                <label htmlFor ="gender"> Gender:</label>
                <input type ="gender" name ="gender" value ={gender} onChange ={(e) => setGender(e.target.value)} required />

                <button type ="submit">Submit</button>
            </form>
        )
      }
    </div>
  );
}

export default Googleoauth;
