import React, { useEffect, useState } from "react";
import { googleLogout, useGoogleLogin } from '@react-oauth/google';
import { useNavigate } from 'react-router';

const Fetchdata = () => {
  const userProfile = JSON.parse(localStorage.getItem("profile"));
  const [profile, setProfile] = useState({
    name: userProfile.name,
    firstName: userProfile.given_name,
    lastName: userProfile.family_name,
    email:userProfile.email
  });
  const [data, setData] = useState([]);
  const [page,setPage] = useState(1);
  const [limit,setLimit] = useState(2);
  const [isNext,setisNext] = useState(true);
  const [totalResult,setTotalResult] = useState(0);
  const navigate = useNavigate();
  const [searchTitle,setSearchTitle] = useState("");


  const getData = async () => {
    const response = await fetch(`${process.env.REACT_APP_API_URL }/data/fetchdata?page=${page}&limit=${limit}`, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
      }
    }
    );
    let temp = await response.json();
    if(temp.length===0)
     {setisNext(false)}
    setData(temp.User);
    setTotalResult(Number(temp.totalResults))
  };

  const changePage = (type) => {
    if(type === "prev"){
      setPage(old => old - 1);
    }
    else if(type === "next"){
      setPage(old => old + 1);
    }
  }

  const updateData = () =>{
    navigate('/data/updatedata');
  }

  const logout =() =>{
    googleLogout();
    localStorage.clear();
    navigate('/');
    setData(null);
    setProfile(null);
  }

  useEffect(() => {
    getData();
  }, [page]);



  

  return (
    <div className ="container mt-5" >

      <div style ={{backgroundColor : "lightgrey"}}>
        <div className ="container d-flex justify-content-between align-items-center my-3">
        <h5>{userProfile.name}</h5>
        <h5> {userProfile.email}</h5>
       {<img src ={userProfile.picture}/>}
        </div>
      
        <div className ="container d-flex justify-content-between my-3">
      <input type = {"text" || "tel"} placeholder ="Search Here" onChange={(e)=>setSearchTitle(e.target.value)}/>
      
      <button className="btn btn-primary" onClick={updateData}>Update Profile</button>
      </div>
      </div>
      <table className ="table table-bordered table-hover" style ={{borderColor : "ActiveBorder"}} margin = "20px">
        <thead>
          <tr border ="2px">
            <th scope ="col" > Name </th>
            <th  scope ="col" >First Name </th>
            <th scope ="col" >Last Name </th>
            <th scope ="col" >image </th>
            <th scope ="col" >email </th>
            <th scope ="col" >Mobile No. </th>
            <th scope ="col" >Gender </th>
            <th scope ="col" >Date of Birth </th>
            <th scope ="col" >Login Time </th>
          </tr>
        </thead>
        <tbody>
        {data.filter((value)=>{
          if(searchTitle === "") {
            return value;
          }else if(value.name.toLowerCase().includes(searchTitle.toLowerCase()) || 
                   value.firstName.toLowerCase().includes(searchTitle.toLowerCase()) || 
                   value.lastName.toLowerCase().includes(searchTitle.toLowerCase()) || 
                   value.email.toLowerCase().includes(searchTitle.toLowerCase()) ||
                   value.Mobile.toString().includes(searchTitle.toString())){
            return value;
          }
        })
        .map((item) => (
          <tr>
            <td>{item.name}</td>
            <td>{item.firstName} </td>
            <td>{item.lastName} </td>
            <td><img src={item.image} alt ="ProfilePic"/></td>
            <td>{item.email} </td>
            <td>{item.Mobile}</td>
            <td>{item.Gender}</td>
            <td>{item.DateofBirth}</td>
            <td>{item.loginTime}</td>
          </tr>
        ))}
        </tbody>
      </table>
      <div className ="d-flex justify-content-between" style={{width:"100%"}}>
        <button className ="btn btn-primary" onClick = {e => changePage("prev")} disabled ={page === 1}>Previous</button>
            
        <button className ="btn btn-primary" onClick = {e => changePage("next")} disabled = {page+1 > Math.ceil(totalResult/limit)} >Next</button>
       
      </div>
      <button className = "btn btn-danger my-3" onClick={logout}>logout</button>
    </div>
  );
};

export default Fetchdata;
